@Component({
  selector: 'app-stripe',
  templateUrl: './stripe.component.html',
  styleUrls: ['./stripe.component.css'],
})
export class StripeComponent implements OnInit {
  ref = '';
  amt = 0;
  status = 'new'; // new, submitting, success, fail
  error = '';
  stripe;
  card;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    @Inject(DOCUMENT) private document: any,
    private titleService: Title
  ) {}

  async ngOnInit() {
    this.titleService.setTitle('Credit Card Payment');
    this.ref = this.activatedroute.snapshot.paramMap.get('ref');
    this.amt = +this.activatedroute.snapshot.paramMap.get('amt');

    if (!window['Stripe']) {
      await loadScript('https://js.stripe.com/v3/');
      this.initStripe();
    } else {
      this.initStripe();
    }

    // _loadFacebook();
  }

  initStripe() {
    // Create a Stripe client
    // this.stripe = Stripe('pk_live_G8OW05aYsLQymdIRSxJHeYun');
    // this.stripe = Stripe('pk_test_nWzLcw2oqIuynzaLVIeFpH9G');
    // this.stripe = Stripe('pk_test_Qqy4M7vl3rpKwbhVry1lYdn7'); // ministry of data
    this.stripe = Stripe('pk_live_4B83gi5We7se25cEER6PGvfj'); // ministry of data
    // Create an instance of Elements
    const elements = this.stripe.elements();
    // Custom styling can be passed to options when creating an Element.
    // (Note that this demo uses a wider set of styles than the guide below.)

    // Create an instance of the card Element
    this.card = elements.create('card');
    // Add an instance of the card Element into the `card-element` <div>
    this.card.mount('#card-element');

    // Handle real-time validation errors from the card Element.
    this.card.addEventListener('change', event => {
      const displayError = document.getElementById('card-errors');
      if (event.error) {
        displayError.textContent = event.error.message;
      } else {
        displayError.textContent = '';
      }
    });
  }

  // Handle form submission
  async onSubmit() {
    this.status = 'submitting';
    const result = await this.stripe.createToken(this.card);
    if (result.error) {
      // Inform the user if there was an error
      const errorElement = document.getElementById('card-errors');
      errorElement.textContent = result.error.message;
      this.status = 'fail';
    } else {
      // Send the token to your server
      await this.stripeTokenHandler(result.token);
    }
  }

  async stripeTokenHandler(token) {
    token.amount = this.amt;
    token.description = this.ref;

    const results: any = await this.api
      .post('/api1/payment/stripe', token)
      .toPromise();
    console.log(results);
    if (results.status === 'succeeded') {
      this.status = 'success';
      await this._payok();
    } else {
      this.status = `fail`;
      this.error = results.message;
    }
  }

  async _payok() {
    await this.api
      .post(`/api1/fbcommand`, {
        pageid: 'pageid',
        fbid: 'fbid',
        commands: [
          {
            type: 'PAYOK',
          },
        ],
      })
      .toPromise();
  }

  closeWindow() {
    location.href = '/fbclose.html';

    // window['MessengerExtensions'].requestCloseBrowser(
    //   () => console.log('Browser closed.'),
    //   error => console.error('requestCloseBrowser error.', error)
    // );
  }
}
