import { Component, Input, OnInit } from '@angular/core';
import { ApiService } from '../../../shared/api.service';

declare const require: (path: string) => any;

@Component({
  selector: 'app-paylah',
  templateUrl: './paylah.component.html',
  styleUrls: ['./paylah.component.css'],
})
export class PaylahComponent implements OnInit {
  @Input()
  pageid;

  @Input()
  fbid;

  page = '';
  mobile = '92388954';
  results;
  constructor(private api: ApiService) {}

  ngOnInit() {}

  assets(image) {
    return require(`./assets/${image}`);
  }

  async paylahsetup(pageid, fbid, mobile) {
    this.page = 'waiting';
    this.results = await this.api
      .post(`/api1/payment/paylah/setup`, { pageid, fbid, mobile })
      .toPromise();
    this.page = 'done';
  }
}
