import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteComponent } from 'src/app/route/route/route.component';
import { BraintreeComponent } from './braintree/braintree.component';
import { DbsComponent } from './dbs/dbs.component';
import { PaylahComponent } from './paylah/paylah.component';
import { PaymentComponent } from './payment/payment.component';
import { StripeComponent } from './stripe/stripe.component';
import { WirecardComponent } from './wirecard/wirecard.component';
const routes: Routes = [
  {
    path: 'dbs/:orderid',
    component: RouteComponent,
    data: { wrappedComponent: DbsComponent },
    // component: DbsComponent,
  },
  {
    path: 'wirecard/:orderid',
    component: RouteComponent,
    data: { wrappedComponent: WirecardComponent },
    // component: WirecardComponent,
  },
  {
    path: 'paylah/:pageid/:fbid',
    component: RouteComponent,
    data: { wrappedComponent: PaylahComponent },
    // component: PaylahComponent,
  },
  {
    path: ':ref/:amt',
    component: PaymentComponent,
  },
  {
    path: 'braintree/:ref/:amt',
    component: BraintreeComponent,
  },
  {
    path: 'stripe/:ref/:amt',
    component: StripeComponent,
  },
  // {
  //   path: 'paylah/:ref/:amt',
  //   component: PaylahComponent,
  // },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  entryComponents: [DbsComponent, WirecardComponent],
})
export class PaymentRoutingModule {}
