import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from 'src/app/shared/api.service';

declare const require: (path: string) => any;

@Component({
  selector: 'app-dbs',
  templateUrl: './dbs.component.html',
  styleUrls: ['./dbs.component.css'],
})
export class DbsComponent implements OnInit {
  @Input()
  orderid;

  order;
  wirecards;

  constructor(
    private api: ApiService,
    private activatedroute: ActivatedRoute
  ) {}

  async ngOnInit() {
    this.order = await this.api.get(`/api1/orders/${this.orderid}`).toPromise();
    const { pageid, fbid } = this.order;
    this.wirecards = [];
  }

  assets(image) {
    return require(`./assets/${image}`);
  }

  remove() {
    window.confirm('Remove this card ?');
  }

  async paylahcheckout(order) {
    const checkout = (await this.api
      .post(`/api1/payment/paylah/checkout`, { order })
      .toPromise()) as any;
    console.log({ checkout });
    alert(checkout.transactionStatusDesc);
  }
}
