import { DOCUMENT } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { loadScript } from '../../../../libs/htmlutlis';
import { ApiService } from '../../../shared/api.service';

@Component({
  selector: 'app-braintree',
  templateUrl: './braintree.component.html',
  styleUrls: ['./braintree.component.css'],
})
export class BraintreeComponent implements OnInit {
  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    @Inject(DOCUMENT) private document: any
  ) {}

  async ngOnInit() {
    // https://developers.braintreepayments.com/start/hello-client/javascript/v3

    if (!window['braintree']) {
      await loadScript(
        'https://js.braintreegateway.com/web/dropin/1.11.0/js/dropin.min.js'
      );
      console.log(window['braintree']);
      this.init();

      // const script = document.createElement('script');
      // script.src =
      //   'https://js.braintreegateway.com/web/dropin/1.11.0/js/dropin.min.js';
      // script.onload = () => {
      //   console.log(window['braintree']);
      //   this.init();
      // };
      // document.head.appendChild(script);
    } else {
      this.init();
    }
  }

  init() {
    const button = document.querySelector('#submit-button');

    window['braintree'].dropin.create(
      {
        authorization: 'sandbox_bzz8xqgc_xp2553r9hnxkxthh',
        container: '#dropin-container',
      },
      (createErr, instance) => {
        button.addEventListener('click', () => {
          console.log('button.click');
          instance.requestPaymentMethod(
            async (requestPaymentMethodErr, payload) => {
              console.log('requestPaymentMethodErr', requestPaymentMethodErr);
              console.log('payload', payload);
              console.log('this.http', this.api);
              // When the user clicks on the 'Submit payment' button this code will send the
              // encrypted payment information in a variable called a payment method nonce

              const result: any = await this.api
                .post('/api1/braintree', { paymentMethodNonce: payload.nonce })
                .toPromise();
              // Tear down the Drop-in UI
              instance.teardown(teardownErr => {
                if (teardownErr) {
                  console.error('Could not tear down Drop-in UI!');
                } else {
                  console.log('Drop-in UI has been torn down!');
                  // Remove the 'Submit payment' button
                  // $('#submit-button').remove();
                }
              });

              if (result.success) {
                console.log(
                  `<h1>Success</h1>
                  <p>Your Drop-in UI is working! Check your <a href="https://sandbox.braintreegateway.com/login">sandbox Control Panel</a>
                  for your test transactions.</p><p>Refresh to try another transaction.</p>`
                );
              } else {
                console.log(result);
                console.log('<h1>Error</h1><p>Check your console.</p>');
              }
            }
          );
        });
      }
    );
  }
}
