import {
  AfterViewChecked,
  Component,
  ElementRef,
  Input,
  OnInit,
  ViewChild,
} from '@angular/core';
import { ApiService } from '../../../shared/api.service';

@Component({
  selector: 'app-wirecard',
  templateUrl: './wirecard.component.html',
  styleUrls: ['./wirecard.component.css'],
})
export class WirecardComponent implements OnInit, AfterViewChecked {
  @Input()
  orderid;

  @ViewChild('autosubmit')
  autosubmit: ElementRef;
  form;

  constructor(private api: ApiService) {}

  async ngOnInit() {
    this.form = await this.api
      .get(`/api1/payment/wirecard/${this.orderid}`)
      .toPromise();
    setTimeout(() => {
      // this.autosubmit.nativeElement.submit();
    });
  }

  ngAfterViewChecked() {}
}
